import React, {useState, useEffect} from 'react';
import Style from '../../styles/camps.module.scss'


function Camps() {

    const [campData, setCampData] = useState(null)
    const [imageData, setImageData] = useState(null)
    const [selected, setSelected] = useState(null)

    const getCamps = () => {
        let url = `https://api.mediehuset.net/mediesuset/camps`
        fetch(url)
        .then(response => response.json())
        .then(json => setCampData(json))
    }

    const getAllImages = () => {
        let url = `https://api.mediehuset.net/mediesuset/images`
        fetch(url)
        .then(response => response.json())
        .then(json => setImageData(json))
    }

    const handleSelected = (id) => {
        let url = `https://api.mediehuset.net/mediesuset/camps/${id}`
        fetch(url)
        .then(res => res.json())
        .then(json => setSelected(json))
        window.scrollTo(0,0)
    }


    useEffect(() => {
        getAllImages()
        getCamps()
        setSelected(null)
    }, [])

    const getAccess = (ticket) => {
        switch (ticket.length){
            case 6:
                return "Alle armb√•nd"

            case 2: 
                return "4 dages partout armb√•nd"

            case 1: 
                return "4 dages De Luxe partout armb√•nd"
        }
    }

    if (!selected){
    return (
        <section>
            <header>
                <div className={Style.mainbannerimage} style={imageData && {backgroundImage: `url(${imageData.items[7].image})`}}></div>
            </header>
                <h2>CAMPS</h2>
                <p className={Style.introtext}>P√• Mediesuset finder du tre camp omr√•der som hver skiller sig ud med deres specielle stemning om du enten du er til fest og ballade eller komfort og service p√• h√∏jt niveua.
                    P√• alle camp omr√•der er der adgang til forplejning i form af mad og drikke, og toilet og bade faciliteter. 
                    Camp omr√•derne er placeret t√¶t p√• festivalpladsen s√• at vores g√¶ster hurtigt kan komme til og fra omr√•derne i mellem de mange events.
                    Det er muligt at reservere pladser p√• de forskellige camp omr√•der - dog kr√¶ver Camp Kultunaut og De Luxe 4 dages partout armb√•nd og De Luxe Armb√•nd.
                    Reservation kan foretages n√•r du bestiller billet.
                </p>
                <div className={Style.grid}>
                {campData && campData.items.map((item, index) => {
                    return (
                        <div key={index} className={Style.griditem}>
                            <div style={{backgroundImage: `url(${item.image})`}}></div>
                            <h4>{item.name}</h4>
                            <p className={Style.description}>{item.description.substring(0,196)}..</p>
                            <p><b>Adgang: </b> {getAccess(item.tickets)}</p>
                            <p><b>Samet antal pladser: </b> {item.num_people}</p>
                            <p><b>Ledige pladser: </b>{item.num_people - Math.floor(Math.random() * 100)}</p>
                            <button onClick={() => handleSelected(item.id)}> L√ÜS MERE</button>
                        </div>
                    )    
                })}
                </div>        
        </section>
    )
    }

    if (selected){
        return (
            <>
            <header>
                <div onClick={()=>{setSelected(null)}} className={Style.bannerimage} style={{backgroundImage: `url(${selected.item.image})`,   cursor: "pointer"}}></div>
            </header>
            <section className={Style.singlecamp}>
            <h2>{selected.item.name}</h2>
            <div className={Style.singlecamptext}>
            <p>{selected.item.description}</p>
            <b>Adgang:</b>
            <p>{getAccess(selected.item.tickets)}</p>
            <p>Ved enkeltdags armb√•nd skal du huske at tjekke ud den efterf√∏lgende dag inden kl. 10:00</p>
            <b>P√• {selected.item.name} f√•r du:</b>
            <ul>
                {selected.item.facilities.map((item, index)=>{
                    return <li key={index}>{item.title}</li>
                })}
            </ul>
            <b>√Öbningstider:</b>
            <p>Campen √•bner tirsdag kl. 18:00 og lukker igen s√∏ndag kl 14.00, hvor alle skal have forladt omr√•det.</p>
            <b>Antal pladser i alt:</b>
            <p>{selected.item.num_people}</p>
            <b>Antal ledige pladser:</b>
            <p>{selected.item.num_people - Math.floor(Math.random() * 100)}</p>
            </div>
            </section>
            </>
        )
    }
}

export default Camps